import React, { useEffect, useState } from 'react'

import { useTranslation } from 'react-i18next';
export default function Navbar() {


  const { t, i18n } = useTranslation();

//  let [thm,setthm]=useState(localStorage.getItem('theme'))
//  document.body.classList.add(localStorage.getItem('theme'))
//  function sun(){
//   document.body.classList.add('light')
//   localStorage.setItem('theme','light')
//   setthm('light')
//  }
//  function dark(){
//   document.body.classList.remove('light')
//   localStorage.setItem('theme','dark')
//   setthm('dark')
//  }

let [thms,setthms]=useState(localStorage.getItem('mod'))

 
  
  useEffect(()=>{

 if(localStorage.getItem('mod') !=null){
   document.body.classList.add(localStorage.getItem('mod'))
 }

  },[])

  return <>

<nav className="navbar navbar-expand-lg rounded-5 px-4" id='nav'>
  <div className="container-fluid">
    <a className="navbar-brand logo" href="#">{t('Ahmed Toukhy')}</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <i className="fa-solid fa-bars text-light"></i>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mx-auto mb-2 mb-lg-0">
        <li className="nav-item ">
          <a className="nav-link active" aria-current="page" href="#hero">{t('About')}</a>
        </li>
        <li className="nav-item ">
          <a className="nav-link" href="#skills"> {t('Skills')}</a>
        </li>
        <li className="nav-item ">
          <a className="nav-link" href="#projects"> {t('Projects')}</a>
        </li>
        <li className="nav-item ">
          <a className="nav-link" href="#contact"> {t('Contact Us')}</a>
        </li>
        
       
      </ul>

      {/* <div> {thm !== "light" ? <button onClick={sun} className=' btn'><i className="fa-regular fa-moon"></i></button> :
                    <button onClick={dark} className= ' btn'><i className="fa-regular fa-sun"></i></button>}
                </div> */}


     <div>

      {thms == 'light' ? <button onClick={()=>{
setthms('dark')
localStorage.setItem('mod','dark')
document.body.classList.remove('light')  
}}
 className= ' btn'><i className="fa-regular fa-moon"></i></button> : <button onClick={()=>{
  setthms('light')
  localStorage.setItem('mod','light')
  document.body.classList.add('light')


 }} className=' btn'><i className="fa-regular fa-sun"></i></button> }

        

      
    


        </div> 
     

          
       <div className='lang'>

        {i18n.language=='ar' ?  <button onClick={()=>{
          i18n.changeLanguage('en')
        }} className='btn bg-secondary text-light'>En</button> : ''}

        {i18n.language=='en' ? <button  onClick={()=>{
          i18n.changeLanguage('ar')
        }} className='btn bg-secondary text-light ms-2'>Ar</button> : '' }
       
        
       </div>
          
          
      
    </div>
  </div>
</nav>
  
  
  </>
}
