import React from 'react'
import Marquee from "react-fast-marquee";
import pic from '../../src/Assets/PicsArt_08-17-06.12.15.jpg'
import html from '../../src/techImg/HTML5.png'
import css from '../../src/techImg/CSS3.png'
import js from '../../src/techImg/JS.png'
import boot from '../../src/techImg/Bootstrap.png'
import ts from '../../src/techImg/typescript.png'
import figma from '../../src/techImg/Figma.png'
import react from '../../src/techImg/React.png'
import next from '../../src/techImg/nextjs.png'
import saas from '../../src/techImg/Sass.png'
import jquery from '../../src/techImg/jquery.png'
import { useTranslation } from 'react-i18next';

export default function Skills() {
  const { t, i18n } = useTranslation();
  return <>

  <div className='skills px-4 py-5' id='skills'>
    <h1 className='mb-5'>{t('Skills')}</h1>
    <Marquee  gradient={false}
        speed={100}
        pauseOnClick={true}
        delay={0}
        play={true}
        direction="right">
    
      <div>
      <img className='ms-3' src={html} alt="" />
      </div>
      <div>
      <img className='ms-3 pic' src={css} alt="" />
      </div>
      <div>
      <img className='ms-3 pic' src={js} alt="" />
      </div>
      <div>
      <img className='ms-3 pic' src={saas} alt="saas" />
      </div>
      <div>
      <img className='ms-3 pic' src={boot} alt="boot" />
      </div>
      <div>
      <img className='ms-3 pic' src={react} alt="react" />
      </div>
      <div>
      <img className='ms-3 pic' src={next} alt="next" />
      </div>
      <div>
      <img className='ms-3 pic' src={jquery} alt="jquery" />
      </div>
      <div>
      <img className='ms-3 pic' src={figma} alt="figma" />
      </div>
      <div>
      <img className='ms-3 pic' src={ts} alt="ts" />
      </div>
       
 
</Marquee >
  </div>
  
  
  </>
}
